import "./App.css";

function App() {
	const onPlay = () => {
		let voices = [];
		voices = window.speechSynthesis.getVoices();
		const text = document.querySelector("#textInput").value
			? document.querySelector("#textInput").value
			: "Rajendra loves Deepika";

		const voice = voices[2];
		let speech = new SpeechSynthesisUtterance();
		speech.lang = "en";
		speech.text = text;
		speech.rate = 0.8;
		speech.volume = 0.7;
		speech.pitch = 1;
		speech.voice = voice;
		window.speechSynthesis.speak(speech);
	};

	return (
		<div className="App">
			<div>
				<input
					id="textInput"
					placeholder="Enter to text to speak"
					className="input-text"
					type="text"
				/>
			</div>
			<div>
				<button className="btn-play" onClick={onPlay}>
					Play
				</button>
			</div>
		</div>
	);
}

export default App;
